import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/views/home'

//blog
import blog from '@/views/blog/index.vue'

import tensorflow from '@/views/blog/20197/tensorflow.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },{
      path: '/blog',
      name: 'blog',
      component:blog
    },{
      path: '/tensorflow',
      name: 'tensorflow',
      component:tensorflow
    }
  ]
})



