<template>
  <div class="tensorflow">
    <div class="video"></div>
    <img class="imgpipi" src="../../../assets/pipi.jpg" alt />
    <p class="name">皮皮</p>
    <div class="text">
      <h4>JavaScript的本地存储与数据监听</h4>
      <p>我这里是讲我的学习和使用过程，主要是思路问题和技术讨论。</p>
      <!-- <h5>使用方法</h5>
      <p>脚本直接引入</p>
      <pre class="Code">&lt;script src="https://cdn.jsdelivr.net/npm/@tensorflow/tfjs@1.0.0/dist/tf.min.js"&gt;&lt;/script&gt;</pre>
      <p>npm</p>
      <pre></pre> -->
    </div>
  </div>
</template>
<script>
let a = new Object({
  code: 200,
  data:[
    {
      id:1,
      jname:a
    },
    {
      id:2,
      jname:a
    }
  ]
});
// my video
try {
  let data = {
    code:500,
    data:null
  }
  if(data.code == 200){
    if(!data.data){
      throw "data data null "+data.data
    }
  }else{
    throw "接口code"+data.code
  }
  
   // generates an exception
}
catch (e) {
  console.log('======my video=========')
  console.log(e)
}

let c = 'return function sun(a,b,c){return a*b*c}'
let z = new Function(c);
console.log(z()(1,2,3));
export default {
  name: "blog",
  data() {
    return {};
  }
};
</script>
<style lang="less" scoped>
.tensorflow {
  background-color: rgba(255, 255, 255, 0.2);
  padding-top: 1px;
  padding-bottom: 1px;
  width: 100vw;
  overflow: hidden;
  min-height: 100vh;
}
.video {
  width: 100vw;
  height: 100vh;
  background-image: url(../../../assets/preview.jpg);
  background-position: center;
  background-size: cover;
  z-index: -1;
  position: fixed;
  top: 0;
}
.imgpipi {
  width: 100px;
  border-radius: 50px;
  display: block;
  margin: 50px auto 10px;
}
.name {
  text-align: center;
  font-size: 30px;
  color: #fff;
  margin-bottom: 40px;
}
h4 {
  line-height: 40px;
}
.text {
  width: 1000px;
  margin: 0 auto;
}
.Code{
  background-color: #454c4a;
  color: #fff;
}
@media (min-width: 650px) and (max-width: 1200px) {
  .text {
    width: 90vw;
    margin: 0 auto;
  }
}
@media (min-width: 250px) and (max-width: 650px) {
  .text {
    width: 95vw;
    margin: 0 auto;
  }
}
</style>


